import Link from "next/link";
import { ReactNode } from "react";

type HeaderProps = {
  rightActions?: ReactNode;
  variant?: "default" | "landing";
};

export default function Header({ rightActions, variant = "default" }: HeaderProps) {
  return (
    <header
      style={{
        height: variant === "landing" ? "72px" : "64px",
        display: "flex",
        alignItems: "center",
        background:
          variant === "landing"
            ? "linear-gradient(180deg, rgba(241,245,249,0.6), rgba(255,255,255,0.85))"
            : "linear-gradient(180deg, rgba(255,255,255,0.85), rgba(255,255,255,0.75))",
        backdropFilter: "saturate(140%) blur(8px)",
        WebkitBackdropFilter: "saturate(140%) blur(8px)",
        borderBottom: "1px solid rgba(226,232,240,0.8)",
        position: "sticky",
        top: 0,
        zIndex: 30,
      }}
    >
      <div
        style={{
          width: "100%",
          maxWidth: variant === "landing" ? "1200px" : "1120px",
          margin: "0 auto",
          padding: "0 20px",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          gap: "16px",
        }}
      >
        <div style={{ display: "flex", alignItems: "center", gap: "16px" }}>
          <Link
            href="/"
            style={{
              color: "#0f172a",
              textDecoration: "none",
              fontWeight: 800,
              letterSpacing: "-0.02em",
              fontSize: variant === "landing" ? "20px" : "18px",
            }}
          >
            Finance CV AI
          </Link>
          <nav style={{ display: "flex", gap: "8px" }}>
            <Link
              href="/cv"
              style={{
                textDecoration: "none",
                color: "#0f172a",
                background: "#f1f5f9",
                border: "1px solid #e2e8f0",
                padding: variant === "landing" ? "10px 14px" : "8px 12px",
                borderRadius: "999px",
                fontWeight: 600,
              }}
            >
              Générateur
            </Link>
            <Link
              href="/a-propos"
              style={{
                textDecoration: "none",
                color: "#0f172a",
                background: "#f1f5f9",
                border: "1px solid #e2e8f0",
                padding: variant === "landing" ? "10px 14px" : "8px 12px",
                borderRadius: "999px",
                fontWeight: 600,
              }}
            >
              À propos
            </Link>
          </nav>
        </div>
        <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
          {rightActions ?? (
            <Link
              href="/cv"
              style={{
                textDecoration: "none",
                background:
                  variant === "landing"
                    ? "linear-gradient(90deg, #0ea5e9, #2563eb)"
                    : "linear-gradient(90deg, #2563eb, #1d4ed8)",
                color: "#ffffff",
                padding: variant === "landing" ? "12px 16px" : "10px 14px",
                borderRadius: "12px",
                fontWeight: 700,
                boxShadow:
                  variant === "landing"
                    ? "0 8px 22px rgba(37, 99, 235, 0.28)"
                    : "0 6px 18px rgba(37, 99, 235, 0.25)",
                border: "1px solid rgba(37,99,235,0.6)",
              }}
            >
              Commencer
            </Link>
          )}
        </div>
      </div>
    </header>
  );
}

